<template>
  <div id="demo8">
    <div v-if="type == 'A'">
      I am A
    </div>
    <div v-if="type == 'B'">
      I am B
    </div>
    <div v-if="type == 'C'">
      I am C
    </div>
    <div v-else>
      Not A/B/C
    </div>

    <div v-if="isShow">
      If
    </div>

    <div v-else>
      Else
    </div>

    <div v-show="isShow">
      V-Show
    </div>
  </div>
</template>

<script>
export default {
  name: "Demo8",
  mounted() {
    setInterval(() => {
    //   this.isShow = !this.isShow;
      this.isShow = false;
    }, 1000);
  },
  data() {
    return {
      type: "C",
      isShow: true
    };
  }
};
</script>

