<template>
  <div id="demo9">
    <!-- Using v-for 1 -->
    <ul>
      <li v-for="(item, index) in ['Angular', 'VueJS', 'React']" :key="index">
        {{ item }}
      </li>
    </ul>
    <hr />
    <!-- Using v-for 2 -->
    <ul>
      <li>iOS</li>
      <li>Android</li>
      <li>Flutter</li>
    </ul>

    <hr />
    <!-- Using v-for via Array 1 -->
    <ul>
      <li v-for="(item, index) in ['iOS', 'Android', 'Flutter']" :key="index">
        {{ item }}
      </li>
    </ul>

    <hr />
    <!-- Using v-for via Array 2 -->
    <ul>
      <li v-for="(item, index) in myCourses" :key="index">
        {{ item }}
      </li>
    </ul>

    <hr />
    <!-- Using v-for via Dic 1 -->
    <ul>
      <li v-for="(item, index) in dCourses" :key="index">
        {{ item.title }}, Price {{ item.price }}
      </li>
    </ul>

    <hr />
    <!-- Using v-for via Dic 2 [Destructuring] -->
    <ul>
      <li v-for="({ title, price }, index) in dCourses" :key="index">
        {{ title }}, Price {{ price }}
      </li>
    </ul>

    <hr />

    <input type="text" placeholder="Title" v-model="course.title" /><br />
    <input type="number" placeholder="Price" v-model="course.price" /><br />
    <button @click="addCourse">Submot</button>
    <!-- Using v-for via Dic 2 [Destructuring] -->
    <ul>
      <li v-for="(course, index) in dynamicCourses" :key="index">
        {{ course.title }}, Price {{ course.price }}
        <button @click="removeCourse(index)">X</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Demo9",
  methods: {
    addCourse() {
      this.dynamicCourses.push(this.course);
      this.course = { title: "", price: 0 };
    },
    removeCourse(index) {
      this.dynamicCourses.splice(index, 1);
    }
  },
  data() {
    return {
      course: { title: "", price: 0 },
      dynamicCourses: [],
      myCourses: ["iOS", "Android", "Flutter"],
      dCourses: [
        { title: "iOS", price: 500 },
        { title: "Android", price: 1000 },
        { title: "Flutter", price: 4000 }
      ]
    };
  }
};
</script>

<style></style>
