<template>
  <div id="demo5">
    <img v-bind:src="imageUrl" v-bind:height="heightValue" />
    <br />
    <a v-bind:href="Google">Google</a>
    <br />
    <!-- short form -->
    <a :href="Youtube">Youtube</a>
    <div :class="[error]">Web-Application</div>
    <div :class="[error, important]">Web-Application</div>
    <div :class="[fasle ? error : '', important]">Web-Application</div>
    <div :style="{ color, fontSize }">Blinding Style</div>
    <div :style="isActive ? styleObj : ''">Blinding Style Object</div>
    <button @click="isActive = !isActive">ToggleActive</button>
  </div>
</template>

<script>
export default {
  name: "Demo5",
  data() {
    return {
      imageUrl: "https://img-corp.net/assets/img/IMG_logo_big_blue.png",
      heightValue: 50,
      Google: "http://www.google.co.th",
      Youtube: "http://www.youtube.com",
      error: "error_class",
      important: "important_class",
      color: "green",
      fontSize: "30px",
      styleObj: {
        backgroundColor: "yellow",
        color: "green"
      },
      isActive: false
    };
  }
};
</script>

<style>
.error_class {
  background-color: red;
  color: white;
}
.important_class {
  font-weight: 1000;
}

a {
  margin-right: 10px;
}
</style>
