<template>
  <div>
    <h1>Data</h1>
    <button
      @click="
        count++;
        isLogged = true;
      "
    >
      Count++
    </button>
    <button @click="count = 0">Reset</button>
    <span>{{ count }}</span>
    <div>{{ message }}</div>
    <div>{{ isLogged }}</div>
    <input type="text" placeholder="Enter your message" v-model="message" />
    <h2 v-if="message == 'nitipat'">Yahoo</h2>
    <h3 v-if="isLogged">Yahoo</h3>

    <div>{{ account }} {{ account.username }} {{ account.password }}</div>
    <div>{{ courses[0] }}</div>
    <ul>
      <li class="content" v-for="(item, index) in courses" :key="index">{{ item }}</li>
    </ul>
    <span class="title">555</span>
  </div>
</template>

<script>
export default {
  name: "Demo3",
  mounted() {
    this.message = "Welcome to Web Application";
  },
  data() {
    return {
      count: 0,
      message: "",
      isLogged: false,
      account: {
        username: "admin",
        password: "1234"
      },
      courses: ["Angular", "VueJS", "ReactJS", "Fluter", "UNIX"]
    };
  }
};
</script>

<style scoped>
.title{
    color:red;

}
</style>
