<template>
  <div id="demo12">
    <h1 ref="title">Waiting for change by Ref</h1>
    <input
      ref="input"
      type="text"
      placeholder="This will be focused automatically"
    />
  </div>
</template>

<script>
export default {
  name: "Demo12",
  mounted() {
    this.$refs["title"].textContent = "Web-Application";
    this.$refs["input"].focus();
    this.$refs["input"].value = "1234";
  }
};
</script>

