<template>
  <div id="demo4">
    <h2>{{ firstname }}</h2>
    <h2>{{ lastname }}</h2>
    <h2>{{ fullname }}</h2>

    <h1 style="backgound-color:gray">
      Random number from computed propertyy: {{ getRandomno }}
    </h1>
    <h1 style="backgound-color:gray">
      Random number from computed propertyy: {{ getRandomno }}
    </h1>

    <h1>Random number from computed propertyy: {{ getRandomno1() }}</h1>
  </div>
</template>

<script>
export default {
  name: "Demo4",
  methods: {
    getRandomno1: function() {
      return Math.random();
    }
  },
  data() {
    return {
      firstname: "Nitipat",
      lastname: "Chaisuwan"
    };
  },
  computed: {
    fullname() {
      return this.firstname + " " + this.lastname;
    },
    getRandomno() {
      return Math.random();
    }
  }
};
</script>

